/* Footer.css */

/* Conteneur global */
.footer {
    background-color: #1f2937; /* Fond gris foncé */
    color: #f9fafb; /* Texte blanc cassé */
    padding: 2rem 1rem;
    text-align: center;
    font-family: "Arial", sans-serif;
  }
  
  /* Conteneur interne */
  .footer-container {
    max-width: 800px;
    margin: 0 auto;
  }
  
  /* Titre du footer */
  .footer-title {
    font-size: 1.5rem; /* Taille 24px */
    font-weight: 700;
    margin-bottom: 0.5rem;
    color: #3b82f6; /* Couleur bleue */
  }
  
  /* Texte descriptif */
  .footer-text {
    font-size: 1rem; /* Taille 16px */
    margin-bottom: 1.5rem;
    line-height: 1.6;
    color: #e5e7eb; /* Gris clair */
  }
  
  /* Icônes des réseaux sociaux */
  .footer-socials {
    display: flex;
    justify-content: center;
    gap: 1.5rem; /* Espacement entre les icônes */
    margin-bottom: 1.5rem;
  }
  
  .footer-icon {
    color: #f9fafb; /* Couleur blanche */
    font-size: 1.5rem; /* Taille 24px */
    transition: color 0.3s ease, transform 0.3s ease;
  }
  
  .footer-icon:hover {
    color: #3b82f6; /* Couleur bleue au survol */
    transform: scale(1.2); /* Agrandissement */
  }
  
  /* Texte de copyright */
  .footer-copy {
    font-size: 0.875rem; /* Taille 14px */
    color: #9ca3af; /* Gris plus clair */
    margin-top: 1rem;
  }
  